export default {"skybox_frag.glsl":"#ifdef GL_ES\r\nprecision mediump float;\r\n#endif\r\n\r\nuniform samplerCube u_samplerCube;\r\nvarying vec3 v_TexCood;\r\n\r\nvoid main() {\r\n    gl_FragColor = textureCube(u_samplerCube, v_TexCood);\r\n}","skybox_vert.glsl":"attribute vec3 a_Position;\r\n\r\nuniform mat4 u_ViewMatrix;\r\nuniform mat4 u_ProjMatrix;\r\n\r\nvarying vec3 v_TexCood;\r\n\r\nvoid main() {\r\n    vec4 pos = u_ProjMatrix * u_ViewMatrix * vec4(a_Position, 1.0);\r\n    \r\n    gl_Position = pos.xyww; \r\n    v_TexCood = a_Position;\r\n}","standard_gouraud_frag.glsl":"#ifdef GL_ES\r\nprecision mediump float;\r\n#endif\r\n\r\nuniform sampler2D u_sampler2d;  //2\r\nvarying vec2 v_TexCood;  //\r\nvarying vec3 v_color;\r\n\r\nuniform bool u_HasTexture;\r\n\r\nvoid main() {\r\n    vec3 textureColor = vec3(1.0, 1.0, 1.0);\r\n    if (u_HasTexture) {\r\n        textureColor = vec3(texture2D(u_sampler2d, v_TexCood));\r\n    }\r\n    gl_FragColor = vec4(textureColor * v_color, 1.0);\r\n}\r\n","standard_gouraud_vert.glsl":"uniform mat4 u_mvMatrix;\r\nuniform mat4 u_ProjMatrix;\r\nuniform mat4 u_NormalMatrix;\r\n\r\nattribute vec4 a_Normal;\r\nattribute vec4 a_Position;\r\n\r\nstruct Material\r\n{\r\n    vec3 emissive; //\r\n    vec3 ambient;  //\r\n    vec3 diffuse;  //\r\n    vec3 specular; //\r\n    float shininess; //\r\n    float alpha; //\r\n};\r\n\r\nstruct Light\r\n{\r\n    vec4 position; //w==1, w==0\r\n    vec3 color; //\r\n    float intensity; //\r\n\r\n    float scope; //scope\r\n    float attenuate; //\r\n\r\n    vec3 direction; // \r\n    float theta; // \r\n};\r\n\r\nconst int MAX_LIGHT_NUM = 8; //\r\n\r\n//\r\nuniform Light lights[MAX_LIGHT_NUM];\r\n\r\n\r\n//1   2  3\r\nuniform int side;\r\n\r\n//uniform Material frontMaterial; //\r\n//uniform Material backMaterial;  //\r\n//\r\n//Material material;\r\n\r\n\r\nuniform vec3 u_globalAmbient;\r\n\r\nuniform vec3 u_materialEmissive;\r\nuniform vec3 u_materialAmbient;\r\nuniform vec3 u_materialDiffuse;\r\nuniform vec3 u_materialSpecular;\r\nuniform float u_materialShiness;\r\n\r\n//varying vec3 v_ViewFragCood; //\r\n//varying vec3 v_FragNormal;   //\r\nattribute vec2 a_TexCood;\r\nvarying vec2 v_TexCood;  //\r\n\r\nuniform int u_NumLight;\r\n\r\nuniform bool u_HasTexture;\r\n\r\nvarying vec3 v_color;\r\n\r\n\r\nvec3 calculateAmbientColor() {\r\n   if (u_HasTexture) {\r\n       return u_materialAmbient * u_globalAmbient;\r\n   }\r\n   return u_materialEmissive + u_materialAmbient * u_globalAmbient;\r\n}\r\n\r\n\r\nfloat lightIntensity(Light light, float distance) {\r\n    float att = 1.0 / pow(distance, 0.9) * light.attenuate;\r\n    return att * light.intensity;\r\n    //return light.intensity;\r\n}\r\n\r\nvec3 lighting(vec3 fragCoodInView, vec3 viewDirection, vec3 normal) {\r\n    //vec3 textureColor = vec3(1.0, 1.0, 1.0);\r\n    vec3 color = calculateAmbientColor();\r\n\r\n    for (int i = 0; i < MAX_LIGHT_NUM; i++) {\r\n        if (i >= u_NumLight) break; // \r\n        Light light = lights[i];\r\n        bool isDirectionalLight = light.position.w == 0.0; // \r\n        vec3 lightDirection = isDirectionalLight ? normalize( light.position.xyz ) :\r\n            normalize( light.position.xyz / light.position.w - fragCoodInView );\r\n        float angle = max(dot(lightDirection, normal), 0.0); // \r\n        if (angle <= 0.0)continue; //  \r\n\r\n        float intensity = light.intensity;\r\n\r\n        if (!isDirectionalLight) {//\r\n            float distance = distance(fragCoodInView, light.position.xyz);\r\n            if (distance > light.scope) continue; //  \r\n            if (light.theta > 0.0) { // \r\n                float fragTheta = dot(-lightDirection, normalize(light.direction)); // \r\n                fragTheta = max(fragTheta, 0.0);\r\n                if (fragTheta < light.theta) continue; // \r\n            }\r\n            intensity = lightIntensity(light, distance);\r\n        }\r\n\r\n        //(1.0, 1.0, 1.0)1\r\n        color += angle * (intensity * u_materialDiffuse) * light.color;\r\n\r\n        //\r\n        // Phong\r\n        \r\n\r\n        // Blinn-Phong\r\n        vec3 halfDirection = (viewDirection + lightDirection); // \r\n        float halfAngle = max(dot(normalize(halfDirection), normal), 0.0);\r\n        if (halfAngle == 0.0) continue;\r\n        color += pow(halfAngle, u_materialShiness) * (intensity * u_materialSpecular) * light.color;\r\n    }\r\n\r\n    return color;\r\n}\r\n\r\nvoid main()\r\n{\r\n    \r\n\r\n    vec4 pos = u_ProjMatrix * u_mvMatrix * a_Position;\r\n    gl_Position = pos;\r\n    vec3 normal = (u_NormalMatrix * a_Normal).xyz;\r\n    if (side == 2 || side == 3)//\r\n    {\r\n        //if (!gl_FrontFacing)tnormal = -tnormal;\r\n    }\r\n    v_color = lighting(pos.xyz, normalize(-pos).xyz, normalize(normal));\r\n    if (u_HasTexture) {\r\n        v_TexCood = a_TexCood;\r\n    }\r\n}\r\n","standard_phong_frag.glsl":"#ifdef GL_ES\r\nprecision mediump float;\r\n#endif\r\n\r\nstruct Material\r\n{\r\n    vec3 emissive; //\r\n    vec3 ambient;  //\r\n    vec3 diffuse;  //\r\n    vec3 specular; //\r\n    float shininess; //\r\n    float alpha; //\r\n};\r\n\r\nstruct Light\r\n{\r\n    vec4 position; //w==1, w==0\r\n    vec3 color; //\r\n    float intensity; //\r\n\r\n    float scope; //scope\r\n    float attenuate; //\r\n\r\n    vec3 direction; // \r\n    float theta; // \r\n};\r\n\r\nconst int MAX_LIGHT_NUM = 8; //\r\n\r\n//\r\nuniform Light lights[MAX_LIGHT_NUM];\r\n\r\n\r\n//1   2  3\r\nuniform int side;\r\n\r\n//uniform Material frontMaterial; //\r\n//uniform Material backMaterial;  //\r\n//\r\n//Material material;\r\n\r\n\r\nuniform vec3 u_globalAmbient;\r\n\r\nuniform vec3 u_materialEmissive;\r\nuniform vec3 u_materialAmbient;\r\nuniform vec3 u_materialDiffuse;\r\nuniform vec3 u_materialSpecular;\r\nuniform float u_materialShiness;\r\n\r\nvarying vec3 v_ViewFragCood; //\r\nvarying vec3 v_FragNormal;   //\r\n\r\nuniform sampler2D u_sampler2d;  //2\r\nvarying vec2 v_TexCood;  //\r\n\r\nuniform int u_NumLight;\r\n\r\nuniform bool u_HasTexture;\r\n\r\n\r\nvec3 calculateAmbientColor(inout vec3 textureColor) {\r\n   if (u_HasTexture) {\r\n       textureColor = vec3(texture2D(u_sampler2d, v_TexCood));\r\n   }\r\n   vec3 color = u_materialEmissive + textureColor * u_materialAmbient * u_globalAmbient;//\r\n   return color;\r\n}\r\n\r\n\r\nfloat lightIntensity(Light light, float distance) {\r\n    float att = 1.0 / pow(distance, 0.9) * light.attenuate;\r\n    return att * light.intensity;\r\n    //return light.intensity;\r\n}\r\n\r\nvec3 lighting(vec3 fragCoodInView, vec3 viewDirection, vec3 normal) {\r\n    vec3 textureColor = vec3(1.0, 1.0, 1.0);\r\n    vec3 color = calculateAmbientColor(textureColor);\r\n\r\n    for (int i = 0; i < MAX_LIGHT_NUM; i++) {\r\n        if (i >= u_NumLight) break; // \r\n        Light light = lights[i];\r\n        bool isDirectionalLight = light.position.w == 0.0; // \r\n        vec3 lightDirection = isDirectionalLight ? normalize( light.position.xyz ) :\r\n            normalize( light.position.xyz / light.position.w - fragCoodInView );\r\n        float angle = max(dot(lightDirection, normal), 0.0); // \r\n        if (angle <= 0.0)continue; //  \r\n\r\n        float intensity = light.intensity;\r\n\r\n        if (!isDirectionalLight) {//\r\n            float distance = distance(fragCoodInView, light.position.xyz);\r\n            if (distance > light.scope) continue; //  \r\n            if (light.theta > 0.0) { // \r\n                float fragTheta = dot(-lightDirection, normalize(light.direction)); // \r\n                fragTheta = max(fragTheta, 0.0);\r\n                if (fragTheta < light.theta) continue; // \r\n            }\r\n            intensity = lightIntensity(light, distance);\r\n        }\r\n\r\n        //(1.0, 1.0, 1.0)1\r\n        color += textureColor * angle * (intensity * u_materialDiffuse) * light.color;\r\n\r\n        //\r\n        // Phong\r\n        \r\n\r\n        // Blinn-Phong\r\n        vec3 halfDirection = (viewDirection + lightDirection); // \r\n        float halfAngle = max(dot(normalize(halfDirection), normal), 0.0);\r\n        if (halfAngle == 0.0) continue;\r\n        color += textureColor * pow(halfAngle, u_materialShiness) * (intensity * u_materialSpecular) * light.color;\r\n    }\r\n\r\n    return color;\r\n}\r\n\r\nvoid main()\r\n{\r\n    vec3 tnormal = normalize(v_FragNormal);\r\n    if (side == 2 || side == 3)//\r\n    {\r\n        if (!gl_FrontFacing)tnormal = -tnormal;\r\n    }\r\n    gl_FragColor = vec4(lighting(v_ViewFragCood, normalize(-v_ViewFragCood), tnormal), 1.0);\r\n}\r\n","standard_phong_vert.glsl":"uniform mat4 u_mvMatrix;\r\nuniform mat4 u_ProjMatrix;\r\nuniform mat4 u_NormalMatrix;\r\n\r\nattribute vec4 a_Normal;\r\nattribute vec4 a_Position;\r\n\r\nvarying vec3 v_ViewFragCood;\r\nvarying vec3 v_FragNormal;\r\n\r\n//\r\nattribute vec2 a_TexCood;\r\nvarying vec2 v_TexCood;\r\n\r\nuniform bool u_HasTexture;\r\n\r\nvoid main()\r\n{\r\n    vec4 modelViewCood = u_mvMatrix * a_Position;\r\n    gl_Position = u_ProjMatrix * modelViewCood;\r\n    v_FragNormal = (u_NormalMatrix * a_Normal).xyz;\r\n    v_ViewFragCood = modelViewCood.xyz;\r\n    if (u_HasTexture) {\r\n        v_TexCood = a_TexCood;\r\n    }\r\n}\r\n"}