import Light from "./Light";
import Vector3 from "../math/Vector3";

/**
 * Created by yaozh on 2017/6/6.
 * attenuate
 */
export default class PointLight extends Light {
  private _scope: number;//
  private _attenuate: number;//

  public constructor(scope?: number, attenuate?: number, color?: Vector3, intensity?: number) {
    super(color, intensity);
    this._scope = scope || 100;
    this._attenuate = attenuate || 10;

    this._type = 'PointLight';
  }

  public get scope():number {
    return this._scope;
  }

  public get attenuate():number {
    return this._attenuate;
  }

}